import React, {useEffect} from 'react';
import './App.css';
import dora1 from './img/dora1.png';
import doraLeft from './img/doraLeft.png';
import doraRight from './img/doraRight.png';
import floor from './img/floor.png';
import floorBad from './img/floorBad.png';
import backgroundImage from './img/forest.png';

const CANVAS_HEIGHT = 800;
const CANVAS_WIDTH = 800;

class BlockGood {
  constructor(factor) {
    this.width = 160;
    this.height = 30;
    this.position = {
      x: 0,
      y: 0//posY - this.height
    }
    this.answer = (Math.floor(Math.random() * 12) + 1) * factor;
    this.img = document.getElementById("floor");
    this.loaded = 0;
  }

  draw(ctx) {
    // ctx.fillStyle = ("rgb(0, 0, 0)");
    // ctx.fillRect(this.position.x, this.position.y, this.width, this.height);
    //let txt = (Math.floor(Math.random() * 12) + 1) * factor;
    this.img.addEventListener('load', e => {
      ctx.drawImage(this.img, this.position.x, this.position.y, this.width, this.height);
      this.loaded += 1;
    });
    if (this.loaded !== 0) {
      ctx.drawImage(this.img, this.position.x, this.position.y, this.width, this.height);      
      let txt = this.answer;
      ctx.font = (this.height-10) + "px Arial";
      ctx.fillStyle = "white";
      ctx.fillText(txt, this.position.x + 60, this.position.y + 24)
    }
  }
}

class BlockBad {
  constructor(factor) {
    this.width = 160;
    this.height = 30;
    this.id = 0;
    this.hit = 0;
    this.position = {
      x: 0,
      y: 0
    }    
    this.colour = "rgb(0, 0, 0)";
    this.answer = (Math.floor(Math.random() * 12) + 1) * factor + Math.floor(Math.random() * (factor-2) + 1);
    this.imgGood = document.getElementById("floor");
    this.imgBad = document.getElementById("floorBad");
    this.img = this.imgGood;
    this.loaded = 0;
  }

  draw(ctx) {
    this.img.addEventListener('load', e => {
      ctx.drawImage(this.img, this.position.x, this.position.y, this.width, this.height);
      this.loaded += 1;
    });
    if (this.loaded !== 0) {
      ctx.drawImage(this.img, this.position.x, this.position.y, this.width, this.height);      
      let txt = this.answer;
      ctx.font = (this.height-10) + "px Arial";
      ctx.fillStyle = "white";
      ctx.fillText(txt, this.position.x + 60, this.position.y + 24)
    }
  }    
}

class Menu {
  constructor() {
    this.gameState = 0;
  }
  draw (ctx) {
    if (this.gameState === -1) {
      ctx.rect(0, 0, CANVAS_WIDTH, CANVAS_HEIGHT);
      ctx.fillStyle = "rgba(0,0,0,0.5)";
      ctx.fill();

      ctx.font = "30px Arial";
      ctx.fillStyle = "white";
      ctx.textAlign = "center";
      ctx.fillText("GAME OVER", CANVAS_WIDTH / 2, CANVAS_HEIGHT / 2);
    }
  }
}

class Player {
  constructor() {
    this.imageStill = document.getElementById("dora1");
    this.imageLeft = document.getElementById("doraLeft");
    this.imageRight = document.getElementById("doraRight");
    this.image = this.imageStill;
    this.width = this.image.width;
    this.height = this.image.height;
    this.position = {
      x: 200,
      y: CANVAS_HEIGHT-5 - this.height
    }
    this.speed = 5;
    this.movement = 0;
    this.gravity = 0.2;
    this.gravitySpeed = 0;
    this.onPlatform = 0;
    this.buffer = 1;
    this.loaded = 1;

    this.score = 0;
    
    this.printTest = 0;
    // https://static.wikia.nocookie.net/doratheexplorer/images/1/10/3.png/revision/latest?cb=20160704175809
    
  }

  draw(ctx) {
    this.imageStill.addEventListener('load', e => {
      ctx.drawImage(this.imageStill, this.position.x, this.position.y, this.width, this.height);      
      this.loaded += 1;
    });
    this.imageLeft.addEventListener('load', e => {
      ctx.drawImage(this.imageLeft, this.position.x, this.position.y, this.width, this.height);      
      this.loaded += 1;
    });
    this.imageRight.addEventListener('load', e => {
      ctx.drawImage(this.imageRight, this.position.x, this.position.y, this.width, this.height);      
      this.loaded += 1;
      
    });
    if (this.loaded !== 0) {
      ctx.drawImage(this.image, this.position.x, this.position.y, this.width, this.height);
    }
  }

  update(deltatime, goodBlocks, badBlocks, menu) {
    if (menu.gameState !== -1) {
      // left/right player movement
      this.position.x += this.movement;    
      if (this.position.x + this.width > CANVAS_WIDTH) {
        this.position.x = CANVAS_WIDTH - this.width;
      }
      if (this.position.x < 0) {
        this.position.x = 0;
      }
    }    

    if (menu.gameState === 1) {
      this.position.y += 1;
      for (let i = 0; i < goodBlocks.length; i++) {
        // check if player has landed on a block
        if (this.position.x + this.width > goodBlocks[i].position.x 
          && this.position.x < goodBlocks[i].position.x + goodBlocks[i].width
          && Math.abs(this.position.y + this.height - goodBlocks[i].position.y) < 15
          && (this.gravitySpeed > 0 || this.onPlatform === 1)
        ) {
          this.imgSrc = "still";
          this.onPlatform = 1;
          this.gravitySpeed = 0;
          this.position.y = goodBlocks[i].position.y - this.height - this.buffer;
          let tmp = goodBlocks[0]
          goodBlocks[0] = goodBlocks[i];
          goodBlocks[i] = tmp;
          i = goodBlocks.length; 
        } else {
          this.onPlatform = 0;
        }
      }

      // move each block down
      for (let i = 0; i < goodBlocks.length; i++) {
        goodBlocks[i].position.y += 1;
        if (goodBlocks[i].position.y > CANVAS_HEIGHT) {
          this.score += 1;
          document.getElementById("scoreID").innerHTML = "Score: " + this.score;
          goodBlocks[i].position.y = -goodBlocks[i].height;
        }
      }
  
      // check if player steps on bad block
      for (let i = 0; i < badBlocks.length; i++) {
        badBlocks[i].position.y += 1;
        if (this.position.x + this.width > badBlocks[i].position.x 
          && this.position.x < badBlocks[i].position.x + badBlocks[i].width
          && Math.abs(this.position.y + this.height - badBlocks[i].position.y) < 15
          && this.gravitySpeed > 0
        ) {
          if (badBlocks[i].hit === 0) {
            badBlocks[i].hit = 1;            
            //badBlocks[i].colour = "rgb(255, 0, 0)";
            badBlocks[i].img = badBlocks[i].imgBad;
            let a = new Audio("https://codeskulptor-demos.commondatastorage.googleapis.com/pang/pop.mp3");
            a.play();
            i = 0;            
          }
        }
      }

      
      // make player fall
      if (this.onPlatform === 0) {
        this.gravitySpeed += this.gravity;
        this.position.y += this.gravitySpeed;
        if (this.gravitySpeed > 0) {
          if (this.position.y > CANVAS_HEIGHT - this.height) { // hit the ground (bottom of screen)
            this.score += 1;
            document.getElementById("scoreID").innerHTML = "Score: " + this.score;
            this.position.y = CANVAS_HEIGHT-5 - this.height;
            this.gravitySpeed = 0; 
            menu.gameState = -1; // game over
            console.log("game over");  
            let backgroundMusic = document.getElementById("backgroundMusic");
            backgroundMusic.pause();    
            let gameOver = document.getElementById("gameOverMusic");
            gameOver.play();
          }
        }        
      } else {
        this.gravitySpeed = 0;
      }
  
      // make bad blocks fall
      for (let i = 0; i < badBlocks.length; i++) {
        if (badBlocks[i].hit !== 0) {
          if (badBlocks[i].hit > 10) {
            badBlocks[i].hit = 10;
          }
          badBlocks[i].position.y += badBlocks[i].hit;
          badBlocks[i].hit += 1;
        }      
        if (badBlocks[i].position.y > CANVAS_HEIGHT) {
          this.score += 1;
          document.getElementById("scoreID").innerHTML = "Score: " + this.score;
          badBlocks[i].position.y = -badBlocks[i].height;
          badBlocks[i].hit = 0;
          //badBlocks[i].colour = "rgb(0, 0, 0)";
          badBlocks[i].img = badBlocks[i].imgGood;
        }
      }
    }
  }

  moveLeft() {
    this.movement = -this.speed;
    this.image = this.imageLeft;
  }

  moveRight() {
    this.movement = this.speed;
    this.image = this.imageRight;
  }

  jump(menu) {    
    if (menu.gameState !== -1) {
      //this.imgSrc = "img/dora2.png";
      this.onPlatform = 0;
      if (this.gravitySpeed === 0) { // prevents spamming 
        this.gravitySpeed = -8;
        let a = new Audio("http://freesoundeffect.net/sites/default/files/jump-video-game-03-sound-effect-32293702.mp3");
        a.play();
      }
      menu.gameState = 1;
    }    
  }

  stop() {
    this.movement = 0;
    this.image = this.imageStill;
  }
}

const App = () => {
  
  let player = "";
  let canvas, ctx = "";
  let goodBlocks = [];
  let badBlocks = [];
  let factor = Math.floor(Math.random() * 11) + 2; // 2 - 12

  useEffect(() => { 
    //let backgroundMusic = new Audio("http://codeskulptor-demos.commondatastorage.googleapis.com/descent/background%20music.mp3");
    let backgroundMusic = document.getElementById("backgroundMusic");
    backgroundMusic.play();
    backgroundMusic.loop = true;
    console.log("beginning v 1.16");
    document.getElementById("headingID").innerHTML = "Which numbers are divisible by " + factor;
    document.getElementById("scoreID").innerHTML = "";
    startGame();
  });

  function startGame() {
    canvas = document.getElementById("gameScreen");
    ctx = canvas.getContext("2d");
    player = new Player();     
    player.draw(ctx);
    
    console.log("starting");
    
    let lastBlockX = CANVAS_WIDTH/2;
    let lastBlockY = CANVAS_HEIGHT;

    for (let block = 0; block < 8; block++) {
      let b = new BlockGood(factor);
      let xVar = Math.floor(Math.random() * 120) + 60;
      if (Math.floor(Math.random() * 10) % 2 === 0) {
        xVar *= -1;
      }
      if (lastBlockX + xVar < 0) {
        lastBlockX -= xVar;
      } else if (lastBlockX + xVar + b.width > CANVAS_WIDTH) {
        lastBlockX -= xVar;
      } else {
        lastBlockX += xVar;
      }

      let yVar = Math.floor(Math.random() * 30) + 90;
      lastBlockY -= yVar;
      b.position.x = lastBlockX;
      b.position.y = lastBlockY - b.height;
      goodBlocks.push(b)
    }
    
    for (let block = 0; block < 10; block++) {
      let b = new BlockBad(factor);
      b.position.x = Math.floor(Math.random() * (CANVAS_WIDTH - b.width) + 10) + 5;
      b.position.y = Math.floor(Math.random() * (CANVAS_HEIGHT - b.height) - 10) + 5;
      // check blocks do not intersect
      let intersects = 0;
      for (let c = 0; c < goodBlocks.length; c++) {
        if (b.position.x > goodBlocks[c].position.x - goodBlocks[c].width
          && b.position.x < goodBlocks[c].position.x + goodBlocks[c].width
          && b.position.y > goodBlocks[c].position.y - goodBlocks[c].height
          && b.position.y < goodBlocks[c].position.y + goodBlocks[c].height
        ) {
          intersects = 1;
          c = goodBlocks.length;
        }
      }
      if (intersects === 0) {
        for (let c = 0; c < badBlocks.length; c++) {
          if (b.position.x > badBlocks[c].position.x - badBlocks[c].width
            && b.position.x < badBlocks[c].position.x + badBlocks[c].width
            && b.position.y > badBlocks[c].position.y - badBlocks[c].height
            && b.position.y < badBlocks[c].position.y + badBlocks[c].height
          ) {
            intersects = 1;
          }
        }
      }
      if (intersects === 1) {
        block -= 1;
      } else {
        badBlocks.push(b);
      }
    }
  }
  const menu = new Menu(); 

  document.addEventListener("keydown", event => {
    if (player !== "") {    
      switch (event.key) {          
        case "ArrowLeft":
          player.moveLeft();
          break;
        case "ArrowRight":
          player.moveRight();
          break;
        case " ":
          player.jump(menu);
          break;
        default:
      }
    }
  });
  
  document.addEventListener("keyup", event => {
    if (player !== "") {   
      switch (event.key) {
        case "ArrowLeft":
          if (player.movement < 0) player.stop();
          break;
        case "ArrowRight":
          if (player.movement > 0) player.stop();
          break;
        default:
      }
    }    
  });
  
  let lastTime = 0;
  let backgroundLoaded = 0;
  function gameLoop(timestamp) {
    let deltatime = timestamp - lastTime;
    lastTime = timestamp;
    if (ctx !== "") {
      ctx.clearRect(0, 0, CANVAS_WIDTH, CANVAS_HEIGHT);
      // load backgrond img
      let background = document.getElementById("backgroundImage");
      if (backgroundLoaded === 0) {
        background.addEventListener('load', e => {
          ctx.drawImage(background, 0, 0, CANVAS_WIDTH, CANVAS_HEIGHT);
          backgroundLoaded = 1;
        });
      } else {
        ctx.drawImage(background, 0, 0, CANVAS_WIDTH, CANVAS_HEIGHT);
      }

      if (player !== "") {
        player.update(deltatime, goodBlocks, badBlocks, menu);
        player.draw(ctx);
      } 
      
      for (let i = 0; i < badBlocks.length; i++) {
        badBlocks[i].draw(ctx);
      }    
      for (let i = 0; i < goodBlocks.length; i++) {
        goodBlocks[i].draw(ctx);
      } 
      menu.draw(ctx);
    }
    requestAnimationFrame(gameLoop);
  }
  requestAnimationFrame(gameLoop);

  function reset() {
    console.log("reset");
    window.location.reload(true);
  }

  return (
    <div className="App">    
      <audio id="backgroundMusic" controls src="https://codeskulptor-demos.commondatastorage.googleapis.com/descent/background%20music.mp3"></audio>
      <audio id="gameOverMusic" controls src="https://www.myinstants.com/media/sounds/super-mario-death-sound-sound-effect.mp3"></audio>

      <h3 id="headingID">test</h3>
      <h4 id="scoreID">test</h4>
      <canvas id="gameScreen" width="800" height="800"></canvas>
      <img id="dora1" src={dora1} style={{display:"none"}} alt="dora" height="100" width="70"></img>
      <img id="doraRight" src={doraRight} style={{display:"none"}} alt="doraRight" height="100" width="70"></img>
      <img id="doraLeft" src={doraLeft} style={{display:"none"}} alt="doraLeft" height="100" width="70"></img>
      <img id="floor" src={floor} style={{display:"none"}} alt="floor" ></img>
      <img id="floorBad" src={floorBad} style={{display:"none"}} alt="floorbad" ></img>
      <img id="backgroundImage" src={backgroundImage} style={{display:"none"}} alt="backgroundimage" ></img>
      <br />
      <button onClick={reset}>Play Again?</button>
    </div>
  );
}

export default App;
